import { Link } from "gatsby"
import React from "react"
import UpIcon from "../svg/up"

interface HeaderProps {
  siteTitle?: string
  currentPage?: string
  currentSubPage?: string
}

export default function Header({
  currentPage = "",
  siteTitle = "",
  currentSubPage = "",
}: HeaderProps) {
  const title = currentPage ?? siteTitle ?? "Home"
  return (
    <div>
      <div className="wrap">
        <div className="scroll-top">
          <a
            id="scroll-top"
            onClick={() => {
              window.scrollTo({ top: 0 })
            }}
          >
            <UpIcon color={"#121212"} />
          </a>
        </div>
        <div className="left-frame-top">
          <div className="panel-1"></div>
          <div className="panel-2"></div>
        </div>
        <div className="right-frame-top">
          <div className="banner">
            {currentPage === "Home"
              ? "Maria Mateescu \u2022 Software Engineer"
              : currentPage + " \u2022 " + currentSubPage}
          </div>
          <div className="data-cascade-button-group">
            <div className="button" id="menu-1">
              <Link to="/">Home</Link>
            </div>
            <div className="button" id="menu-2">
              <Link to="/scales/">Music</Link>
            </div>
            <div className="button" id="menu-3">
              <Link to="/theatre/">Interests</Link>
            </div>
            <div className="button" id="menu-4">
              <Link to="/blog/">Blog</Link>
            </div>
          </div>
          <div className="runner">
            <div className="top-corner-bg">
              <div className="top-corner"></div>
            </div>
            <div className="block-2"></div>
            <div className="block-3"></div>
            <div className="block-4">
              <div className="block-4-floatbar"></div>
            </div>
          </div>
          <div className="bar-panel">
            <div className="bar-1"></div>
            <div className="bar-2"></div>
            <div className="bar-3"></div>
            <div className="bar-4">
              <div className="bar-4-inside"></div>
            </div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
  )
}

export function SubMenu({
  currentPage = "",
  siteTitle = "",
  currentSubPage = "",
}: HeaderProps) {
  if (currentPage === "Music") {
    return (
      <div className="left-frame">
        <div>
          <div className={"panel-33"}></div>
          <div className={"panel-3"}>
            <Link to="/scales/">Scales</Link>
          </div>
          <div className={"panel-4"}>
            <Link to="/chords/">Chords</Link>
          </div>
          {currentSubPage === "Scales " && (
            <>
              <div className="panel-5"></div>
              <div className="panel-6"></div>
              <div className="panel-7"></div>
              <div className="panel-8"></div>
            </>
          )}
        </div>
        <div>
          <div className="panel-10"></div>
        </div>
      </div>
    )
  }

  if (currentPage === "Interests") {
    return (
      <div className="left-frame">
        <div>
          <div className={"panel-33"}></div>
          <div className={"panel-3"}>
            <Link to="/theatre/">Theatre</Link>
          </div>
          <div className={"panel-4"}>
            <Link to="/reading/">Reading</Link>
          </div>
          <div className={"panel-3"}>
            <Link to="/art/">Art</Link>
          </div>
              <div className="panel-5"></div>
              <div className="panel-6"></div>
              <div className="panel-7"></div>
              <div className="panel-8"></div>
        </div>
        <div>
          <div className="panel-10"></div>
        </div>
      </div>
    )
  }

  if (currentPage === "Home") {
    return (
      <div className="left-frame">
        <div>
          <div className="panel-3"></div>
          <div className="panel-6"></div>
          <div className="panel-4"></div>
        </div>
        <div>
          <div className="panel-10"></div>
        </div>
      </div>
    )
  }

  return (
    <div className="left-frame">
      <div>
        <div className="panel-3"></div>
        <div className="panel-4"></div>
        <div className="panel-5"></div>
        <div className="panel-6"></div>
        <div className="panel-7"></div>
        <div className="panel-8"></div>
        <div className="panel-9"></div>
      </div>
      <div>
        <div className="panel-10"></div>
      </div>
    </div>
  )
}
