/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Header, { SubMenu } from "./header"

interface LayoutProps {
  currentPage?: string
  children: React.ReactNode
  currentSubPage?: string
}

export default function Layout({
  currentPage = "",
  currentSubPage = "",
  children,
}: LayoutProps) {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  return (
    <>
      <Header
        currentPage={currentPage}
        siteTitle={data.site.siteMetadata?.title || `Title`}
        currentSubPage={currentSubPage}
      />
      <div className="wrap" id="gap">
        <SubMenu
          currentPage={currentPage}
          currentSubPage={currentSubPage}
          siteTitle={data.site.siteMetadata?.title || `Title`}
        />
        <div className="right-frame">
          <div className="bar-panel">
            <div className="bar-6"></div>
            <div className="bar-7"></div>
            <div className="bar-8"></div>
            <div className="bar-9">
              <div className="bar-9-inside"></div>
            </div>
          </div>
          <div className="runner">
            <div className="corner-bg">
              <div className="corner"></div>
            </div>
          </div>
          <div className="content">{children}
          
      <footer className="page-footer go-center">
        <div className="lcars-bar-slice-top"></div>
        <div className="lcars-bar">
          <div className="lcars-bar-inner">
            <div className="lcars-bar-cutout"></div>
          </div>
        </div>
        Â© {new Date().getFullYear()} Maria Mateescu, Built with
        {` `}
        <a href="https://www.gatsbyjs.com">Gatsby</a>, style inspired by{" "}
        <a href="https://thelcars.com">LCARS</a>
      </footer></div>
          
        </div>
      </div>
    </>
  )
}
